export default class trainRoute {
  constructor(route = {}, seat = {}) {
    // 出发
    this.DepartureDateTime = route.DepartureDateTime ? route.DepartureDateTime : 0; // "DepartureDateTime": 1540333560,
    this.DepartStationCode = route.DepartStationCode ? route.DepartStationCode : ''; // "DepartStationCode": "AOH",
    this.DepartStationName = route.DepartStationName ? route.DepartStationName : ''; // "DepartStationName": "上海虹桥",
    // 到达
    this.ArrivalDateTime = route.ArrivalDateTime ? route.ArrivalDateTime : 0; // "ArrivalDateTime": 1540355340,
    this.ArrivalStationCode = route.ArrivalStationCode ? route.ArrivalStationCode : ''; // "ArrivalStationCode": "VNP",
    this.ArrivalStationName = route.ArrivalStationName ? route.ArrivalStationName : ''; // "ArrivalStationName": "北京南",
    // 其他信息
    this.TrainNo = route.TrainNo ? route.TrainNo : ''; // "TrainNo": "5l0000G10271",
    this.TrainCode = route.TrainCode ? route.TrainCode : ''; // "TrainCode": "G102",
    this.RunTime = route.RunTime ? route.RunTime : ''; //  "RunTime": "06:03";
    this.RunTimeMinute = route.RunTimeMinute ? route.RunTimeMinute : ''; // "RunTimeMinute": "363",
    this.StartStationName = route.StartStationName ? route.StartStationName : ''; // "StartStationName": "上海虹桥",
    this.EndStationName = route.EndStationName ? route.EndStationName : ''; // "EndStationName": "北京南",
    this.TrainCategory = route.TrainCategory ? route.TrainCategory : ''; // "TrainCategory": "G/C",
    this.ArrivalDays = route.ArrivalDays ? route.ArrivalDays : 0; // "ArrivalDays": 0
    // 选中座位号
    this.Count = seat.Count ? seat.Count : 0;
    this.Price = seat.Price ? seat.Price : 0;
    this.TypeCode = seat.TypeCode ? seat.TypeCode : '';
    this.TypeName = seat.TypeName ? seat.TypeName : ''
    this.ChooseSeats = ''
  }
}



