<template>
  <div class="hs-container">
    <c-header isBackNav>
      <div slot="middle" class="header-title">
        <h2>{{isTitle}}</h2>
        <p v-if="routeCount>0">{{$t('base.XTrips', 'function', routeCount)}}</p>
      </div>

      <div slot="right" class="standard">

        <!-- 差旅标准 -->
        <p v-if="!trainOrder.OriginalOrderID && Number(trainOrder.TravelType)=== 0" @click="showTravelStandard = true">{{$t('base.Policy')}}</p>

        <!-- 变更到站 -->
        <p v-else-if="originalDetails.CanChangeStation" @click="showWhereBack = true">变更到站</p>

      </div>
    </c-header>

    <!-- 日期选择栏 -->
    <div class="select-data-container" ref="select-data-fixed">
      <div class="left-day" @click="isCanFirstDate?'' : _setChangeDate(isCurrentDate,-1)">
        <span :class="{gray:isCanFirstDate}">{{$t('base.Yesterday')}}</span>
      </div>
      <div class="middle-day" @click="showDatePicker=true">
        <p>{{beTime(new Date(isCurrentDate).getTime(), 'MM月dd日') + '  ' +getWeeks(isCurrentDate)}} </p>
        <p class="linear"></p>
        <img src="static/image/train/calendar.png">
      </div>
      <div class="right-day" @click="isCanLastDate?'' : _setChangeDate(isCurrentDate,1)">
        <span :class="{gray:isCanLastDate}">{{$t('base.Tomorrow_2')}}</span>
      </div>
    </div>

    <!-- 列表 -->
    <hs-scroll
      class="table-view"
      ref="tableView"
      :data="details.Routes"
      :isListenScroll="true"
      :options="tableViewOptions"
      @scrollStart="_scrollStart"
      @touchEnd="_touchEnd"
      @pulling-down="_getDetailsList"
    >
      <div :style="{height: '50px', width: '100%'}"></div>
      <hs-null v-show="routeCount === 0"></hs-null>
      <hs-section v-show="routeCount > 0">
        <infor-cell v-for="(Route,index) in details.Routes" :key="index"
         :currentRoute="Route"
         :conditionsTime="isConditionsTime"
         :filters="details.Filters"
         @delegateForExtend="_clickExtend"
         @delegateForTableTime="_clickTableTime"
         ></infor-cell>
      </hs-section>
      <div :style="{height: '55px', width: '100%'}"></div>
    </hs-scroll>

    <!-- footer -->
    <div class="footer" ref="footer">
      <div class="item" @click="[condition.depate=!condition.depate,condition.arrive=undefined,condition.time=undefined]">
        <div class="logo" v-show="condition.depate === undefined" style="background-image: url('static/image/train/depate.png')"></div>
        <div class="logo" v-show="condition.depate === true" style="background-image: url('static/image/plane-ticket/book/down.png')"></div>
        <div class="logo" v-show="condition.depate === false" style="background-image: url('static/image/plane-ticket/book/up.png')"></div>
        <p>{{`${condition.depate === undefined ? $t('base.Departure') : (condition.depate === true ? $t('base.DepartureEarliest_2') : $t('base.DepartureLatest_2'))}`}}</p>
      </div>
      <div class="item" @click="[condition.depate=undefined,condition.arrive=!condition.arrive,condition.time=undefined]">
        <div class="logo" v-show="condition.arrive === undefined" style="background-image: url('static/image/train/arrive.png')"></div>
        <div class="logo" v-show="condition.arrive === true" style="background-image: url('static/image/plane-ticket/book/down.png')"></div>
        <div class="logo" v-show="condition.arrive === false" style="background-image: url('static/image/plane-ticket/book/up.png')"></div>
        <p>{{`${condition.arrive === undefined ? $t('base.Arrival') : (condition.arrive === true ? $t('base.ArrivalEarliest') : $t('base.ArrivalLatest'))}`}}</p>
      </div>
      <div class="item" @click="[condition.depate=undefined,condition.arrive=undefined,condition.time=!condition.time]">
        <div class="logo" v-show="condition.time === undefined" style="background-image: url('static/image/train/time.png')"></div>
        <div class="logo" v-show="condition.time === true" style="background-image: url('static/image/plane-ticket/book/down.png')"></div>
        <div class="logo" v-show="condition.time === false" style="background-image: url('static/image/plane-ticket/book/up.png')"></div>
        <p>{{`${condition.time === undefined ? $t('base.TimeConsuming') : (condition.time === true ? $t('base.TimeConsumingLeast') : $t('base.TimeConsumingMost'))}`}}</p>
      </div>
      <div class="item" @click="[showFilter = true]">
        <div class="logo" style="background-image: url('static/image/plane-ticket/book/screen.png')">
          <div :class="{dot:isHaveFilters}"></div>
        </div>
        <p>{{$t('base.Filters')}}</p>
      </div>
    </div>

    <!-- 车次详情 -->
    <query-list-details
      v-if="showSeatDetails"
      :extendRoute="extendRoute"
      :title="isTitle"
      @delegateBook="_bookRoute"
      @delegateForTableTime="_clickTableTime"
    >
    </query-list-details>

    <!-- 时刻表 -->
    <c-table-time v-if="showTableTime" :params="currentTableTime"></c-table-time>

    <!-- 筛选 -->
    <hs-filter-train @confirmFliter="_confirmFliter" v-model="showFilter" :filters="details.Filters"></hs-filter-train>

    <!-- 地址选择器 -->
    <hs-address-picker v-model="showWhereBack" :switchList="[$t('base.City')]" :title="$t('base.ArrivalCity')" @select="_setAddressWhereBack"></hs-address-picker>

    <!-- 日期选择器 -->
    <hs-date-picker v-if="trainRight"
      v-model="showDatePicker"
      :date="isCurrentDate"
      @changeDate="_setChangeDate"
      :overdueDate=" `${(this.isGoRoute || this.isOneWay)?beTime(new Date()) : trainOrder.DepartDate }`"
      :maxToDataDays="trainRight.CanBookDays"
      :title="$t('base.DepartureDate')">
    </hs-date-picker>

    <!-- 差旅标准 -->
    <c-travel-standard v-model="showTravelStandard" :details="trainRight.TrainRank" :travelPolicy="trainRight.TravelPolicy" :type='3'></c-travel-standard>
  </div>
</template>

<script type='text/ecmascript-6'>
import CHeader from 'components/c-header/c-header'
import HsAddressPicker from 'base/hs-address-picker/hs-address-picker'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsNull from 'base/hs-null/hs-null'
import HsDatePicker from 'base/hs-date-picker/hs-date-picker'
import CTravelStandard from 'components/c-travel-standard/c-travel-standard'
import HsDialog from 'base/hs-dialog/hs-dialog'
import { mapGetters, mapMutations } from 'vuex'
import { kpiTrainGetTrainRouteList, kpiTrainGetHotelIndexInitInfo } from 'api'
import TrainRoute from 'common/model/train/trainRoute'
export default {
  beforeRouteEnter(to, from, next) {
    console.log('from: ', from)
    next(vm => {
      if (from.path === '/') {
        vm.$router.replace('/home')
      } else if (from.path === '/train/query') {
        vm.details = { Routes: [], Filters: [] }
        vm._getTrainRight()
        vm._getDetailsList()

      // 改签
      } else if (
        from.path === '/train/orderDetail' ||
        from.path === '/train/change'
      ) {
        vm._getDatalistWithChange()
      } else {
        vm.details = JSON.parse(JSON.stringify(vm.details))
      }
    })
  },
  beforeRouteLeave(to, from, next) {
    if (this.showTableTime) {
      this.showTableTime = false
      next(false)
    } else if (this.showSeatDetails || this.showDatePicker || this.showTravelStandard) {
      this.showSeatDetails = false
      this.extendRoute = {}
      this.showDatePicker = false
      this.showTravelStandard = false
      next(false)
    } else {
      next()
    }
  },
  computed: {
    ...mapGetters(['trainOrder']),
    // 判断是不是单程
    isOneWay() {
      return `${this.$route.query.isOneWay}` === 'true'
    },
    // 是否为去程
    isGoRoute() {
      return this.noValue(this.trainOrder.GoSelectedRoute.TrainCode)
    },
    // 是否为回程程
    isBackRoute() {
      return this.noValue(this.trainOrder.BackSelectedRoute.TrainCode)
    },
    // 当前出发时间
    isCurrentDate() {
      return (this.isGoRoute || this.isOneWay)
        ? this.trainOrder.DepartDate
        : this.trainOrder.ArrivalDate
    },
    // 是否有前一天
    isCanFirstDate() {
      if (this.isGoRoute || this.isOneWay) {
        return this.beTime(new Date()) === this.isCurrentDate
      } else {
        return this.trainOrder.DepartDate === this.isCurrentDate
      }
    },
    // 是否有前一天
    isCanLastDate() {
      return this.beTime(new Date().setDate(new Date().getDate() + this.trainRight.CanBookDays)) === this.isCurrentDate
    },
    // 判断回程时间不可大于去程时间
    isConditionsTime() {
      return (this.isGoRoute || this.isOneWay) ? 0 : this.trainOrder.GoSelectedRoute.ArrivalDateTime
    },
    routeCount() {
      return this.details.Routes ? this.details.Routes.length : 0
    },
    // 因公 因私
    isTravelType() {
      return Number(this.trainOrder.TravelType) === 0 ? this.$t('base.Business') : this.$t('base.Personal')
    },
    // 标题
    isTitle() {
      if (this.isGoRoute || this.isOneWay) {
        return this.trainOrder.DepartObject[`CityName_${this.language}`] + '-' + this.trainOrder.ArrivalObject[`CityName_${this.language}`] + '(' + this.isTravelType + ')'
      } else {
        return this.trainOrder.ArrivalObject[`CityName_${this.language}`] + '-' + this.trainOrder.DepartObject[`CityName_${this.language}`] + '(' + this.isTravelType + ')'
      }
    },
    // 是否有筛选
    isHaveFilters() {
      if (this.noValue(this.details.Filters)) {
        return false
      }
      for (let filter of this.details.Filters) {
        for (let item of filter.Items) {
          if (item.select) {
            return true
          }
        }
      }
      return false
    },
    /** 改签原订单 */
    originalDetails() {
      return JSON.parse(this.$route.query.originalDetails)
    }
  },
  watch: {
    condition: {
      handler: function(val) {
        if (val.depate !== undefined) {
          // 出发时间
          this.details.Routes.sort((a, b) => {
            return val.depate ? (a['DepartureDateTime'] - b['DepartureDateTime']) : (b['DepartureDateTime'] - a['DepartureDateTime'])
          })
        } else if (val.arrive !== undefined) {
          // 到达时间
          this.details.Routes.sort((a, b) => {
            return val.arrive ? (a['ArrivalDateTime'] - b['ArrivalDateTime']) : (b['ArrivalDateTime'] - a['ArrivalDateTime'])
          })
        } else if (val.time !== undefined) {
          // 耗时
          this.details.Routes.sort((a, b) => {
            return val.time ? (Number(a['RunTimeMinute']) - Number(b['RunTimeMinute'])) : (Number(b['RunTimeMinute']) - Number(a['RunTimeMinute']))
          })
        }
      },
      deep: true
    }
  },
  data() {
    return {
      showFilter: false,
      showSeatDetails: false,
      showTableTime: false,
      showDatePicker: false,
      showTravelStandard: false,
      details: {
        Routes: [],
        Filters: []
      },
      extendRoute: {}, // 当前点击展开项
      currentTableTime: {}, // 当前点击时刻表
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      condition: {
        depate: undefined,
        arrive: undefined,
        time: undefined
      },
      trainRight: {},
      /** 变更到站 */
      showWhereBack: false
      /*******************/
    }
  },
  methods: {
    ...mapMutations([
      'SET_TRAIN_ORDER',
      'SET_TRAIN_CHANGE'
    ]),
    /** 获取火车票差旅标准 */
    _getTrainRight() {
      kpiTrainGetHotelIndexInitInfo().then((res) => {
        console.log('机票权限查询页面：', res)
        this.trainRight = res.ResultData
      })
    },
    // 点击预订
    _bookRoute(route, seat) {
      let res = JSON.parse(JSON.stringify(this.trainOrder))
      if (this.isOneWay || this.isGoRoute) {
        res.GoSelectedRoute = new TrainRoute(route, seat)
        this.SET_TRAIN_ORDER(res)
        this.showSeatDetails = false
        this.extendRoute = {}

        if (this.trainOrder.OriginalOrderID) {
          console.log('跳转到改签选择乘客页面')
          this.$router.push({path: '/train/change', query: {originalDetails: JSON.stringify(this.originalDetails)}})
        } else if (this.isOneWay) {
          // this.$router.push({ path: '/train/book', query: { isExamine: true } })
          this.$router.push('/train/book')
        } else {
          this._getDetailsList()
        }
      } else {
        res.BackSelectedRoute = new TrainRoute(route, seat)
        this.SET_TRAIN_ORDER(res)
        this.showSeatDetails = false
        this.extendRoute = {}
        // this.$router.push({ path: '/train/book', query: { isExamine: true } })
        this.$router.push('/train/book')
      }
    },
    //  获取改期数据
    // _getChangeDetail() {
    //   console.log('GoSelectedRoute: ', this.trainOrder)
    //   let params = {
    //     // this.trainOrder.GoSelectedRoute.TrainCode,
    //     TrainCode: this.$route.query.TrainCode,
    //     OrderID: this.$route.query.orderID
    //   }
    //   kpiTrainGetChangeInitInfo(params).then((res) => {
    //     console.log('改签详情', res)
    //     let isChange = res.ResultData.Route.Tickets.filter(item => item.IsChangeUnHandled === false)
    //     if (isChange.length > 0) {
    //       this.SET_TRAIN_CHANGE(res)
    //       this.$router.push({ path: '/train/changeRefund', query: {isExamine: true, Id: 2, orderID: this.$route.query.orderID, TrainCode: this.$route.query.TrainCode, TravelType: this.trainOrder.TravelType} })
    //     } else {
    //       this.$toast(this.$t('TM.NoChangeablePassengers'))
    //     }
    //     console.log('是否可改签', isChange)
    //   })
    // },
    // 点击班次展开座位
    _clickExtend(route, startTop) {
      this.extendRoute = JSON.parse(JSON.stringify(route))
      this.$set(this.extendRoute, 'startTop', startTop)
      this.showSeatDetails = true
    },
    // 当前点击时刻表
    _clickTableTime(item) {
      this.currentTableTime = {
        DepartStationCode: item.DepartStationCode,
        DepartStationName: item.DepartStationName,
        ArrivalStationCode: item.ArrivalStationCode,
        ArrivalStationName: item.ArrivalStationName,
        DepartDate: item.DepartureDateTime,
        TrainNo: item.TrainNo,
        TrainCode: item.TrainCode
      }
      this.showTableTime = true
    },
    _scrollStart() {
      this.$refs.footer && (this.$refs.footer.style.bottom = '-50px')
    },
    _touchEnd() {
      this.$refs.footer && (this.$refs.footer.style.bottom = '0px')
    },
    // 确认筛选
    _confirmFliter(filters) {
      this.details.Filters = filters
    },
    // 更改时间
    _setChangeDate(date = this.isCurrentDate, day = 0) {
      date = this.beTime(new Date(date).setDate(new Date(date).getDate() + day))
      console.log('选择时间=' + date)
      let res = JSON.parse(JSON.stringify(this.trainOrder))
      if (this.isOneWay || this.isGoRoute) {
        res.DepartDate = date
        if (new Date(date).getTime() > new Date(this.trainOrder.ArrivalDate).getTime()) {
          res.ArrivalDate = this.beTime(new Date(date).setDate(new Date(date).getDate() + 1))
        }
      } else {
        res.ArrivalDate = date
      }
      this.SET_TRAIN_ORDER(res)
      this._getDetailsList()
    },
    // 查询航班列表
    _getDetailsList() {
      this.details = { Routes: [], Filters: [] }
      let params = (this.isOneWay || this.isGoRoute) ? {
        DepartStationCode: this.trainOrder.DepartStationCode,
        ArrivalStationCode: this.trainOrder.ArrivalStationCode,
        DepartDate: new Date(this.trainOrder.DepartDate).getTime() / 1000,
        IsOnlyHighSpeed: this.trainOrder.isHighSpeed
      } : {
        DepartStationCode: this.trainOrder.ArrivalStationCode,
        ArrivalStationCode: this.trainOrder.DepartStationCode,
        DepartDate: new Date(this.trainOrder.ArrivalDate).getTime() / 1000,
        IsOnlyHighSpeed: this.trainOrder.isHighSpeed
      }
      kpiTrainGetTrainRouteList(params).then((res) => {
        console.log(res)
        this.details = res.ResultData
        this.details.Filters.forEach((filter, filterIndex) => {
          filter.Items.forEach((item, index) => {
            this.$set(item, 'select', false)
          })
        })
        // console.log(this.details.Filters)
      }).catch(() => {
        this.$refs.tableView.forceUpdate()
      })
    },

    /** 改签 */
    _getDatalistWithChange() {
      console.log('originalDetails: ', this.originalDetails)
      this._getDetailsList()
    },

    /** 变更到站 */
    _setAddressWhereBack(City) {
      let res = {...this.trainOrder}
      res.ArrivalObject = City
      res.ArrivalStationCode = City.CityCode
      res.ArrivalStationName = City[`CityName_${this.language}`]
      this.SET_TRAIN_ORDER(res)
      this._getDetailsList()
    }
  },
  components: {
    CHeader,
    HsAddressPicker,
    HsScroll,
    HsSection,
    HsCell1,
    HsNull,
    HsDialog,
    HsDatePicker,
    CTravelStandard,
    InforCell: () => import('./infor-cell'),
    QueryListDetails: () => import('./query-list-details'),
    HsFilterTrain: () => import('base/hs-filter/hs-filter-train'),
    CTableTime: () => import('components/c-table-time/c-table-time')
  }
}
</script>

<style lang='stylus' scoped='' type='sylesheet/stylus'>
@import '~common/stylus/variable'
@import '~common/stylus/base'
.hs-container
  width 100%
  height 100%
  .header-title
    @extend .flex-column
    @extend .flex-center
    >h2
      font-size 1.1rem
      font-weight bold
      text-align center
    >p
      font-size 0.6rem
      color $color_gray_text
      line-height 1.5em
  .header-title[type="EN"]
    >h2
      font-size 0.8rem
  .standard
    font-size 0.9rem
    color $color_black_text_0
  .select-data-container
    position absolute
    left 0
    top 45px
    width 100%
    height 50px
    background-color $color_white
    padding 10px 15px
    font-size 0.8rem
    z-index 1
    border-top 10px solid $color_gray_background_0
    border-bottom 1px solid $color_gray_background_0
    @extend .flex-row
    @extend .flex-between
    >div
      @extend .flex-row
      @extend .flex-center
      .gray
        color $color_gray_text
    img
      width 18px
      height 18px
    .right-day
      >img
        transform rotate(180deg)
    .middle-day
      @extend .flex-row
      @extend .flex-center
      background-color $color_theme
      border-radius 12px
      padding 5px 10px
      color $color_white
      .linear
        width 1px
        height 18px
        margin 0px 5px
        background-color $color_white
      >img
        width 18px
        height 18px
  .table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden
  >.footer
    @extend .flex-between
    width 100%
    height 50px
    background-color $color-black_1
    position absolute
    bottom 0
    transition all 300ms
    >.item
      @extend .flex-column
      @extend .flex-center
      flex 1
      color $color_white
      font-size 0.8rem
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        margin-bottom 5px
        position relative
        >.dot
          position absolute
          top 0
          right 0
          background-color $color_theme
          width 6px
          height 6px
          border-radius 3px
      >.red
        color $color_theme
</style>



